    <div class='dizhiguanli'>
        <div class="title">{{$t('dizhiguanli[0]')}}</div>
        <div class="hr"></div>
        <div class="xinzeng">{{$t('dizhiguanli[1]')}}</div>
        <div class="item">
            <div>{{$t('xinzeng[3]')}}：</div>
            <div v-if="apis.lang == 'cn'">
                <el-select v-model="guojia1" filterable  :placeholder="$t('xinzeng[5]')" @change="selectA()">
                    <el-option
                    v-for="item in guojia"
