                </el-select>
            </div>
            <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('dizhiguanli[2]')}}：</div>
            <div class="shengshi">
                <div v-if="apis.lang == 'cn'">
                    <el-select v-model="sheng1" filterable :placeholder="$t('dizhiguanli[3]')" @change="selectB()">
                        <el-option
                        v-for="item in sheng"
                        :key="item.id"
                        :label="item.cname == '' ? item.code : item.cname"
                        :value="item.id"
                        v-if="item.country_id == guojia1 && item.name">
                        </el-option>
                    </el-select>
                </div>
                <div v-if="apis.lang == 'en'">
                    <el-select v-model="sheng1" filterable :placeholder="$t('dizhiguanli[3]')" @change="selectB()">
                        <el-option
                        v-for="item in sheng"
                        :key="item.id"
                        :label="item.name == '' ? item.code : item.name"
