                        </el-option>
                    </el-select>
                </div>
                <div v-if="apis.lang == 'cn'">
                    <el-select v-model="shi1" filterable :placeholder="$t('dizhiguanli[4]')" @change="selectC()">
                        <el-option
                        v-for="item in shi"
                        :key="item.id"
                        :label="item.cname == '' ? item.code : item.cname"
                        :value="item.id"
                        v-if="item.state_id == sheng1 && item.cname">
                        </el-option>
                    </el-select>
                </div>
                <div v-if="apis.lang == 'en'">
                    <el-select v-model="shi1" filterable :placeholder="$t('dizhiguanli[4]')" @change="selectC()">
                        <el-option
                        v-for="item in shi"
                        :key="item.id"
                        :label="item.name == '' ? item.code : item.name"
                        :value="item.id"
                        v-if="item.state_id == sheng1 && item.name">
                        </el-option>
                    </el-select>
                </div>
                <div  v-if="apis.lang == 'cn'">
                    <el-select v-model="qu1" :placeholder="$t('dizhiguanli[5]')">
                        <el-option
                        v-for="item in qu"
                        :key="item.id"
                        :label="item.cname == '' ? item.code : item.cname"
                        :value="item.id"
                        v-if="item.city_id == shi1 && item.cname">
                        </el-option>
                    </el-select>
                </div>
                <div  v-if="apis.lang == 'en'">
                    <el-select v-model="qu1" :placeholder="$t('dizhiguanli[5]')">
                        <el-option
                        v-for="item in qu"
                        :key="item.id"
                        :label="item.name == '' ? item.code : item.name"
                        :value="item.id"
                        v-if="item.city_id == shi1 && item.name">
                        </el-option>
                    </el-select>
                </div>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('xinzeng[4]')}}：</div>
            <div>
                <el-input v-model="formData.postcode" :placeholder="$t('xinzeng[6]')"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item textarea">
            <div>{{$t('dizhiguanli[6]')}}:</div>
            <div>
                <el-input
                    resize='none'
                    type="textarea"
                    :placeholder="$t('dizhiguanli[7]')"
                    v-model="formData.address">
                </el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <!-- <div class="item">
            <div>邮政编码：</div>
            <div>
                <el-input v-model="input" placeholder="填写邮政编码"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div> -->
        <div class="item">
            <div>{{$t('dizhiguanli[8]')}}：</div>
            <div>
                <el-input v-model="formData.name" :placeholder="$t('dizhiguanli[9]')"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item phone">
            <div>{{$t('dizhiguanli[10]')}}：</div>
            <div>
                <!-- <div>
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div> -->
                <div>
                    <el-input v-model="formData.phone" :placeholder="$t('dizhiguanli[11]')"></el-input>
                </div>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div></div>
            <div>
                <input type="checkbox" v-model="checked" id="ch" />
			    <label for="ch">{{$t('dizhiguanli[12]')}}</label>
            </div>
            <div class="zhanwei"></div>
        </div>

        <div class="item itemBtn">
            <div></div>
            <div>
                <div class="btn" @click="addDizhi()">{{$t('dizhiguanli[13]')}}</div>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="dizhiList">
            <div class="title">
                <div>{{$t('dizhiguanli[14]')}}</div>
                <div>{{$t('dizhiguanli[15]')}}</div>
                <div>{{$t('dizhiguanli[16]')}}</div>
                <div>{{$t('dizhiguanli[17]')}}</div>
                <div>{{$t('dizhiguanli[18]')}}</div>
                <div>{{$t('dizhiguanli[19]')}}</div>
                <div></div>
            </div>
            <div class="list">
                <div class="dizhiItem" v-for="(item,i) in address_list" :key="i">
                    <div>{{item.name}}</div>
                    <div>{{item.province}}{{item.city}}{{item.area}}</div>
                    <div>{{item.address}}</div>
                    <div>{{item.postcode}}</div>
                    <div>{{item.phone}}</div>
                    <div>
                        <div @click="xiugai(item.id,item.country)">{{$t('dizhiguanli[20]')}}</div>
                        <div></div>
                        <div @click="shanchu(item.id)">{{$t('dizhiguanli[21]')}}</div>
                    </div>
                    <div>
                        <div v-if="item.status == 1">{{$t('dizhiguanli[22]')}}</div>
                        <div @click="morendizhi(item.id)" v-else>{{$t('dizhiguanli[23]')}}</div>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog :title="$t('dizhiguanli[24]')" :visible.sync="xiugaidizhi" :before-close="handleClose">
            <div class="xiugai">
                <table></table>
                <div class="title">{{$t('dizhiguanli[25]')}}</div>
                <div class="item1">
                    <div>{{$t('dizhiguanli[2]')}}：</div>
                    <div class="shengshi">
                        <div v-if="apis.lang == 'cn'">
                            <el-select v-model="sheng1" filterable :placeholder="$t('dizhiguanli[3]')" @change="selectB()">
                                <el-option
                                v-for="item in sheng"
                                :key="item.id"
                                :label="item.cname == '' ? item.code : item.cname"
                                :value="item.id"
                                v-if="item.country_id == guojia1 && item.name">
                                </el-option>
                            </el-select>
                        </div>
                        <div v-if="apis.lang == 'en'">
                            <el-select v-model="sheng1" filterable :placeholder="$t('dizhiguanli[3]')" @change="selectB()">
                                <el-option
                                v-for="item in sheng"
                                :key="item.id"
                                :label="item.name == '' ? item.code : item.name"
                                :value="item.id"
                                v-if="item.country_id == guojia1 && item.name">
                                </el-option>
                            </el-select>
                        </div>
                        <div v-if="apis.lang == 'cn'">
                            <el-select v-model="shi1" filterable :placeholder="$t('dizhiguanli[4]')" @change="selectC()">
                                <el-option
                                v-for="item in shi"
                                :key="item.id"
                                :label="item.cname == '' ? item.code : item.cname"
                                :value="item.id"
                                v-if="item.state_id == sheng1 && item.cname">
                                </el-option>
                            </el-select>
                        </div>
                        <div v-if="apis.lang == 'en'">
                            <el-select v-model="shi1" filterable :placeholder="$t('dizhiguanli[4]')" @change="selectC()">
                                <el-option
                                v-for="item in shi"
                                :key="item.id"
                                :label="item.name == '' ? item.code : item.name"
                                :value="item.id"
                                v-if="item.state_id == sheng1 && item.name">
                                </el-option>
                            </el-select>
                        </div>
                        <div  v-if="apis.lang == 'cn'">
                            <el-select v-model="qu1" :placeholder="$t('dizhiguanli[5]')">
                                <el-option
                                v-for="item in qu"
                                :key="item.id"
                                :label="item.cname == '' ? item.code : item.cname"
                                :value="item.id"
                                v-if="item.city_id == shi1 && item.cname">
                                </el-option>
                            </el-select>
                        </div>
                        <div  v-if="apis.lang == 'en'">
                            <el-select v-model="qu1" :placeholder="$t('dizhiguanli[5]')">
                                <el-option
                                v-for="item in qu"
                                :key="item.id"
                                :label="item.name == '' ? item.code : item.name"
                                :value="item.id"
                                v-if="item.city_id == shi1 && item.name">
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="zhanwei"></div>
                </div>
                <div class="item1 textarea">
                    <div>{{$t('dizhiguanli[6]')}}</div>
                    <div>
                        <el-input
                            resize='none'
                            type="textarea"
                            :placeholder="$t('dizhiguanli[7]')"
                            v-model="formData.address">
                        </el-input>
                    </div>
                    <div class="zhanwei"></div>
                </div>
                <div class="item1">
                    <div>邮编：</div>
                    <div>
                        <el-input v-model="formData.postcode" placeholder="请填写邮政编码"></el-input>
                    </div>
                    <div class="zhanwei"></div>
                </div>
                <div class="item1">
                    <div>{{$t('dizhiguanli[8]')}}：</div>
                    <div>
                        <el-input v-model="formData.name" :placeholder="$t('dizhiguanli[9]')"></el-input>
                    </div>
                    <div class="zhanwei"></div>
                </div>
                <div class="item1 phone">
                    <div>{{$t('dizhiguanli[10]')}}：</div>
                    <div>
                        <!-- <div>
                            <el-select v-model="value" placeholder="请选择">
                                <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </div> -->
                        <div>
                            <el-input v-model="formData.phone" :placeholder="$t('dizhiguanli[11]')"></el-input>
                        </div>
                    </div>
                    <div class="zhanwei"></div>
                </div>
                <div class="item1">
                    <div></div>
                    <div>
                        <el-checkbox :label="$t('dizhiguanli[12]')" name="type"></el-checkbox>
                    </div>
                    <div class="zhanwei"></div>
                </div>

                <div class="item1 itemBtn">
                    <div></div>
                    <div>
                        <div class="btn" @click="setdizhi()">{{$t('dizhiguanli[13]')}}</div>
                    </div>
                    <div class="zhanwei"></div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import guojia from "../../../../static/json/countries.json"
import sheng from "../../../../static/json/states.json"
import shi from "../../../../static/json/cities.json"
import qu from "../../../../static/json/regions.json"
export default {
    data() {
        return {
            input:'',
            value:'',
            guojia:'',
            sheng:'',
            shi:'',
            qu:'',
            guojia1:'',
            sheng1:'',
            shi1:'',
            qu1:'',
            guojiaText:'',
            shengText:'',
            shiText:'',
            quText:'',
            
            
            checked:false,//是否设为默认
            caty1:'',
            caty2:'',
            caty3:'',
            catyValue1:'',
            catyValue2:'',
            catyValue3:'',
            formData:{
                country:'',
                province:'',
                city:'',
                area:'',//省市区
                address:'',//详细地址
                name:'',//收货人姓名
                phone:'',//
                postcode:'',//邮编
            },
            address_list:'',//地址列表
            get_first_category:'',//地址详情
            get_address_info:'',//修改地址Data
            xiugaidizhi:false,//修改地址loading
            xiugaitype:true,//是否设为默认
            xiugaiId:'',//修改Id
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.guojia = guojia.RECORDS
        _this.sheng = sheng.RECORDS
        _this.shi = shi.RECORDS
        _this.qu = qu.RECORDS
        console.log(_this.guojia,_this.sheng,_this.shi,_this.qu)
        //获取一级城市
        _this.apis.get(_this.apis.get_first_region,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.caty1 = res.data.data
            }
        })
        //获取地址列表
        _this.getAddress();
    },
    watch:{
    },
    methods:{
        //获取地址列表
        getAddress(){
            var _this = this;
            _this.apis.get(_this.apis.address_list,{params:{lang:_this.apis.lang}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.address_list = res.data.data.data
                }   
            })
        },
        //删除地址
        shanchu(id){
            var _this= this;
            _this.$confirm(_this.$t('dizhiguanliTip[0]'), _this.$t('dizhiguanliTip[1]'), {
                confirmButtonText: _this.$t('dizhiguanliTip[2]'),
                cancelButtonText: _this.$t('dizhiguanliTip[3]'),
                type: 'warning'
            }).then(() => {
                _this.apis.get(_this.apis.del_address,{params:{lang:_this.apis.lang,id:id}}).then(function(res) {
                    if(res.data.code == 0 ){
                        console.log(res.data.data)
                        _this.$message({
                            type: 'success',
                            message: _this.$t('dizhiguanliTip[4]'),
                        });
                        _this.getAddress();
                    }
                })
            }).catch(() => {
                _this.$message({
                    type: 'info',
                    message: _this.$t('dizhiguanliTip[5]'),
                });
            });
        },
        //设置默认地址
        morendizhi(id){
            var _this = this;
            _this.$confirm(_this.$t('dizhiguanliTip[6]'), _this.$t('dizhiguanliTip[1]'), {
                confirmButtonText: _this.$t('dizhiguanliTip[2]'),
                cancelButtonText: _this.$t('dizhiguanliTip[3]'),
                type: 'warning'
            }).then(() => {
                 _this.apis.get(_this.apis.set_address_status,{params:{lang:_this.apis.lang,id:id}}).then(function(res) {
                    if(res.data.code == 0 ){
                        _this.$message({
                            type: 'success',
                            message: _this.$t('dizhiguanliTip[7]'),
                        });
                        _this.getAddress();
                    }else{
                        _this.$message({
                            type: 'info',
                            message: res.data.msg
                        });
                    }
                })
            }).catch(() => {
                _this.$message({
                    type: 'info',
                    message: _this.$t('dizhiguanliTip[8]'),
                });
            });
        },
        //修改地址
        xiugai(id,guojia){
            var _this = this;
            for(let i=0;i<_this.guojia.length;i++){
                    if(_this.guojia[i].cname == guojia || _this.guojia[i].name == guojia){
                        _this.guojia1 = _this.guojia[i].id
                    }
            }
            _this.xiugaidizhi = true
            _this.xiugaiId = id;
            _this.apis.get(_this.apis.get_address_info,{params:{lang:_this.apis.lang,id:id}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.get_address_info = res.data.data
                    _this.formData.country = guojia
                    _this.formData.address = res.data.data.address
                    _this.formData.phone = res.data.data.phone
                    _this.formData.name = res.data.data.name
                    _this.formData.postcode = res.data.data.postcode
                }
            })
            _this.apis.get(_this.apis.get_next_region,{params:{lang:_this.apis.lang}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.get_first_category = res.data.data
                }
            })
        },
        //提交修改地址
        setdizhi(){
            var _this = this;
            _this.formData.province = _this.shengText
            _this.formData.city = _this.shiText
            _this.formData.area = _this.quText
            _this.formData.lang = _this.apis.lang
            if(_this.checked){
                _this.formData.status = 1
            }else{
                _this.formData.status = 0
            }
            _this.formData.id = _this.xiugaiId
            _this.apis.post(_this.apis.edit_address,_this.formData).then(function(res) {
                if(res.data.code == 0){
                    _this.formData = {
                            country:'',//省市区
                            province:'',//省市区
                            city:'',//省市区
                            area:'',//省市区
                            address:'',//详细地址
                            name:'',//收货人姓名
                            phone:'',//
                        },
                    _this.$message({
                        type: 'success',
                        message: _this.$t('dizhiguanliTip[10]'),
                    });
                    _this.getAddress()
                    _this.xiugaidizhi = false;
                }
            })
        },
        //修改弹窗关闭事件
        handleClose(){
            var _this = this;
            _this.formData = {
                area:'',//省市区
                address:'',//详细地址
                name:'',//收货人姓名
                phone:'',//
            }
            _this.xiugaidizhi = false;
        },
        selectA(){
            var _this = this;
            _this.sheng1 = ''
            for(let i=0;i<_this.guojia.length;i++){
                if(_this.guojia[i].id == _this.guojia1){
                    if(_this.apis.lang == 'cn'){
                        _this.guojiaText = _this.guojia[i].cname
                    }else if(_this.apis.lang == 'en'){
                        _this.guojiaText = _this.guojia[i].name
                    }
                }
            }
        },
        selectB(){
            var _this = this;
            _this.shi1 = ''
            for(let i=0;i<_this.sheng.length;i++){
                if(_this.guojia[i].id == _this.sheng1){
                    if(_this.apis.lang == 'cn'){
                        _this.shengText = _this.sheng[i].cname
                    }else if(_this.apis.lang == 'en'){
                        _this.shengText = _this.sheng[i].name
                    }
                }
            }
        },
        selectC(){
            var _this = this;
            _this.qu1 =''
            for(let i=0;i<_this.shi.length;i++){
                if(_this.shi[i].id == _this.shi1){
                    if(_this.apis.lang == 'cn'){
                        _this.shiText = _this.shi[i].cname
                    }else if(_this.apis.lang == 'en'){
                        _this.shiText = _this.shi[i].name
                    }
                }
            }
        },
        selectD(){
            var _this = this;
            for(let i=0;i<_this.qu.length;i++){
                if(_this.shi[i].id == _this.qu1){
                    if(_this.apis.lang == 'cn'){
                        _this.quText = _this.qu[i].cname
                    }else if(_this.apis.lang == 'en'){
                        _this.quText = _this.qu[i].name
                    }
                }
            }
        },
        select1(){
            var _this = this
            _this.apis.get(_this.apis.get_next_region,{params:{lang:_this.apis.lang,id:_this.catyValue1}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.caty2 = res.data.data
                }
            })
        },
        select2(){
            var _this = this
            _this.apis.get(_this.apis.get_next_region,{params:{lang:_this.apis.lang,id:_this.catyValue2}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.caty3 = res.data.data
                }
            })
        },
        select3(){
            var _this = this
        },
        //添加地址
        addDizhi(){
            var _this = this
            console.log(_this.type)
            _this.formData.country = _this.guojiaText
            _this.formData.province = _this.shengText
            _this.formData.city = _this.shiText
            _this.formData.area = _this.quText
            _this.formData.lang = _this.apis.lang
            if(_this.checked){
                _this.formData.status = 1
            }else{
                _this.formData.status = 0
            }
            _this.apis.post(_this.apis.add_address,_this.formData).then(function(res) {
                if(res.data.code == 0){
                    _this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    _this.getAddress();
                }else{
                    _this.$message({
                        message: res.data.msg,
                        type: 'warning'
                    });
                }
                _this.formData.status = false;
                _this.formData = {
                    area:'',//省市区
                    address:'',//详细地址
                    name:'',//收货人姓名
                    phone:'',//
                }
            })
        }
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.xiugai{
    width: 930px;
    height: 480px;
    background: #fff;
    .title{
        height: 50px;
        margin: 20px 26px;
        font-size: 16px;
        font-weight: bold;
        color: #2E2D2D;
    }
    .item1{
        display: flex;
        align-items: center;
